<template>
  <div class="Serialrows">
    <div class="comment" :style="{ height: Morecomments ? 'auto' : '' }">
      <div class="commenttop">
        <h4>书友圈</h4>
        <div @click="showPopup">
          <img src="@/assets/img/lt.png" />
          <h4>评论</h4>
        </div>
      </div>
      <Comments v-for="item in serialrows" :key="item.id" :item="item" />
    </div>
    <div class="Morecomments">
      <button @click="Morecomments = !Morecomments">
        {{ Morecomments ? "收起评论" : "更多评论" }}
      </button>
    </div>
    <van-popup v-model="show" position="bottom">
      <div>
        <div @click="showemoji = !showemoji">
          <img
            src="data:image/png;base64,"
          />
          <span>表情</span>
        </div>
        <input type="text" v-model.trim="mycomment" ref="inputTitle" @click="showemoji = false"/>
        <button @click="comment">评论</button>
      </div>
      <div class="emoji" v-show="showemoji">
        <div
          class="emoji_box"
          v-for="item in emojis"
          :key="item.name"
          @click="addemoji(item.name)"
        >
          <img :src="item.path" class="emojis_img" />
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import Comments from "@/components/Comments.vue";
export default {
  name: "Serialrows",
  components: { Comments },
  props: ["serialrows"],
  data() {
    return {
      show: false,
      showemoji: false,
      Morecomments: false,
      mycomment: "",
      emojis: [
        {
          name: "[暗中观察]",
          path: require("@/assets/emojis/10000.png"),
        },
        {
          name: "[拜托]",
          path: require("@/assets/emojis/10001.png"),
        },
        {
          name: "[不高兴]",
          path: require("@/assets/emojis/10002.png"),
        },
        {
          name: "[打霍嗨]",
          path: require("@/assets/emojis/10003.png"),
        },
        {
          name: "[得意]",
          path: require("@/assets/emojis/10004.png"),
        },
        {
          name: "[发刀片]",
          path: require("@/assets/emojis/10005.png"),
        },
        {
          name: "[害怕]",
          path: require("@/assets/emojis/10006.png"),
        },
        {
          name: "[紧张]",
          path: require("@/assets/emojis/10007.png"),
        },
        {
          name: "[沮丧]",
          path: require("@/assets/emojis/10008.png"),
        },
        {
          name: "[开心]",
          path: require("@/assets/emojis/10009.png"),
        },
        {
          name: "[灵光一现]",
          path: require("@/assets/emojis/10010.png"),
        },
        {
          name: "[迷惑]",
          path: require("@/assets/emojis/10011.png"),
        },
        {
          name: "[期待]",
          path: require("@/assets/emojis/10012.png"),
        },
        {
          name: "[生气]",
          path: require("@/assets/emojis/10013.png"),
        },
        {
          name: "[无语]",
          path: require("@/assets/emojis/10014.png"),
        },
        {
          name: "[震惊]",
          path: require("@/assets/emojis/10015.png"),
        },
      ],
    };
  },
  mounted() {},
  methods: {
    showPopup() {
      if (!localStorage.getItem("api_token")) {
        this.$bus.$emit("Iflogin");
        return;
      }
      this.show = true;
      this.$nextTick(function () {
        this.$refs.inputTitle.focus();
      });
    },
    addemoji(name) {
      this.mycomment += name;
    },
    comment() {
      if (this.mycomment) {
        this.$bus.$emit("add_review", this.mycomment, "");
        console.log(this.show);
        this.show = false;
        console.log(this.show);
        this.mycomment = "";
      } else {
        this.$toast("请输入内容再发送");
      }
    },
  },
  watch: {
    show(a) {
      if (!a) {
        this.showemoji = false;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.Serialrows {
  width: 350px;
  // min-height: 285.6px;
  padding-top: 20px;
  margin: 20px 12.6px 0px;
  background-color: #fff;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  .Morecomments {
    text-align: center;
    padding-bottom: 20px;
    button {
      width: 90px;
      height: 24px;
      line-height: 24px;
      text-align: center;
      padding: 0px;
      background-color: #fff;
      border: 1px solid #999;
      border-radius: 10px;
      font-size: 12px;
      color: #999;
    }
  }
  .comment {
    margin: 0px 12px 15px;
    height: 225px;
    overflow: hidden;
    .commenttop {
      margin-bottom: 25px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 21px;
      div {
        display: flex;
        h4 {
          margin-left: 5px;
        }
      }
      h4 {
        font-size: 14px;
        font-weight: bold;
        color: #333;
        line-height: 21px;
      }
      img {
        height: 19px;
        width: 21px;
      }
    }
  }
}
.van-popup {
  width: 100%;
  & > div {
    display: flex;
    align-items: center;
    min-height: 45px;
    div {
      width: 60px;
      margin-left: 10px;
      display: flex;
      justify-content: space-around;
      img {
        width: 15px;
        height: 15px;
      }
      span {
        font-size: 14px;
        color: #999;
      }
    }
    input {
      border: none;
      background-color: #f7f7f7;
      width: 214px;
      height: 30px;
      padding: 0px 10px;
      margin: 0px 5px;
      border-radius: 50px;
    }
    button {
      border: none;
      background-color: #439fff;
      color: #fff;
      font-size: 14px;
      width: 55px;
      height: 25px;
      border-radius: 50px;
    }
  }
}
.emoji {
  display: flex;
  flex-wrap: wrap;
  height: auto;
  .emoji_box {
    display: flex;
    width: 75px;
    justify-content: space-between;
    margin-left: 0px;
    .emojis_img {
      width: 57px;
      height: 57px;
      margin-top: 10px;
    }
  }

  padding-bottom: 25px;
}
</style>