<template>
  <div class="comments">
    <div class="left">
      <img
        :src="
          item.avatar
            ? item.avatar
            : 'https://m.jyacg.com/static/img/defaultHead.4ebeb21.png'
        "
      />
    </div>
    <div class="info_box">
      <div class="info_box_one">
        <span
          >{{ item.nickname }}
          <span v-if="item.level"> lv{{ item.level }}</span></span
        >
        <span @click="showPopup">回复</span>
      </div>
      <h6 class="info_box_two">{{ item.cre_time }}</h6>
      <div class="info_box_s">
        <p v-html="content"></p>
      </div>
      <div v-show="item.child" class="return_info">
        <div v-for="items in item.child" :key="items.id">
          <div>
            <span>{{ items.nickname }}</span>
            {{ items.content }}
          </div>
          <p>
            <span>{{ items.cre_time }}</span>
          </p>
        </div>
      </div>
    </div>
    <van-popup v-model="show" position="bottom">
      <div>
        <input type="text" v-model.trim="myReplycomment" ref="inputReply" />
        <button @click="Replycomment">评论</button>
      </div>
    </van-popup>
  </div>
</template>
item
<script>
export default {
  name: "Comments",
  props: ["item"],
  data() {
    return {
      emojis: [
        {
          name: "[暗中观察]",
          path: require("@/assets/emojis/10000.png"),
        },
        {
          name: "[拜托]",
          path: require("@/assets/emojis/10001.png"),
        },
        {
          name: "[不高兴]",
          path: require("@/assets/emojis/10002.png"),
        },
        {
          name: "[打霍嗨]",
          path: require("@/assets/emojis/10003.png"),
        },
        {
          name: "[得意]",
          path: require("@/assets/emojis/10004.png"),
        },
        {
          name: "[发刀片]",
          path: require("@/assets/emojis/10005.png"),
        },
        {
          name: "[害怕]",
          path: require("@/assets/emojis/10006.png"),
        },
        {
          name: "[紧张]",
          path: require("@/assets/emojis/10007.png"),
        },
        {
          name: "[沮丧]",
          path: require("@/assets/emojis/10008.png"),
        },
        {
          name: "[开心]",
          path: require("@/assets/emojis/10009.png"),
        },
        {
          name: "[灵光一现]",
          path: require("@/assets/emojis/10010.png"),
        },
        {
          name: "[迷惑]",
          path: require("@/assets/emojis/10011.png"),
        },
        {
          name: "[期待]",
          path: require("@/assets/emojis/10012.png"),
        },
        {
          name: "[生气]",
          path: require("@/assets/emojis/10013.png"),
        },
        {
          name: "[无语]",
          path: require("@/assets/emojis/10014.png"),
        },
        {
          name: "[震惊]",
          path: require("@/assets/emojis/10015.png"),
        },
      ],
      show: false,
      myReplycomment: "",
    };
  },
  computed: {
    content() {
      if (this.item.give_num) {
        return `<img src="${this.item.icon}" style='height:40px;'><span>${this.item.give_title}x${this.item.give_num} </span>`;
      }

      let str = this.item.content;
      this.emojis.forEach((item) => {
        if (str.indexOf(item.name) > -1) {
          let Img = `<img src="${item.path}" style='height:40px;'>`;
          str = str.replaceAll(item.name, Img);
        }
      });
      return str;
    },
  },
  methods: {
    Replycomment() {
      if (this.myReplycomment) {
        this.$bus.$emit(
          "add_review",
          this.myReplycomment,
          `&reply_id=${this.item.id}`
        );
        this.show = false;
        this.myReplycomment = "";
      } else {
        this.$toast("请输入内容再发送");
      }
    },
    showPopup() {
      if (!localStorage.getItem("api_token")) {
        this.$bus.$emit("Iflogin");
        return;
      }
      this.show = true;
      this.$nextTick(function () {
        this.$refs.inputReply.focus();
      });
    },
  },
};
</script>

<style lang="scss">
.comments {
  display: flex;
  border-bottom: 1px dotted #aaa;
  margin-top: 15px;
  .left {
    width: 44px;
    img {
      width: 42px;
      height: 42px;
      border-radius: 50%;
    }
  }
  .info_box {
    width: 272px;
    margin: 0px 0px 15px 12px;

    .info_box_one {
      display: flex;
      justify-content: space-between;
      width: 100%;

      span {
        font-size: 15px;
        color: #439fff;
      }
    }
    .info_box_two {
      width: 100%;
      margin: 7.5px 0px;
      font-size: 12px;
      color: #999;
      font-weight: bold;
    }
    .info_box_s {
      margin-bottom: 10px;
      font-size: 15px;
      color: #333;
      p {
        span {
          font-size: 12px;
          font-weight: bold;
          display: inline-block;
          margin-left: 10px;
        }
      }
    }
    .return_info {
      & > div {
        min-height: 47px;
        padding: 5px;
        margin-bottom: 5px;
        width: 100%;
        div {
          font-size: 12px;
          line-height: 15px;
          span {
            font-size: 12px;
            color: #439fff;
            margin-right: 5px;
          }
        }
        p {
          height: 16px;
          margin-top: 5px;
          width: 100%;
          font-size: 12px;
          span {
            width: 48%;
            float: right;
            display: block;
          }
        }
      }
    }
  }
  .van-popup {
    width: 100%;
    & > div {
      display: flex;
      align-items: center;
      min-height: 45px;
      input {
        border: none;
        background-color: #f7f7f7;
        width: 284px;
        height: 30px;
        padding: 0px 10px;
        margin: 0px 5px;
        border-radius: 50px;
      }
      button {
        border: none;
        background-color: #439fff;
        color: #fff;
        font-size: 14px;
        width: 55px;
        height: 25px;
        border-radius: 50px;
      }
    }
  }
}
</style>