<template>
  <div>
    <Titletop class="title"
      ><router-link slot="r" to="/index"></router-link
    ></Titletop>
    <Detailsapptop
      :detailsapptop="detailsapptop"
      :getdetail="getdetail"
    ></Detailsapptop>
    <Serial :serial="serial"></Serial>
    <Serialrows
      :serialrows="serialrows"
      :getcomments="getcomments"
    ></Serialrows>
    <div class="recommend_title">你可能感兴趣的小说</div>
    <Recommendbox :recommendbox="recommendbox"></Recommendbox>
    <Iflogin></Iflogin>
    <MyLoading></MyLoading>
  </div>
</template>

<script>
import Titletop from "@/components/Titletop.vue";
import Detailsapptop from "@/components/Detailsapptop.vue";
import Serial from "@/components/Serial.vue";
import Serialrows from "@/components/Serialrows.vue";
import Recommendbox from "@/components/Recommendbox.vue";
import Iflogin from "@/components/Iflogin.vue";
import MyLoading from "@/components/MyLoading.vue";
export default {
  name: "Mydetails",
  props: ["id"],
  components: {
    Titletop,
    Detailsapptop,
    Serial,
    Serialrows,
    Recommendbox,
    Iflogin,
    MyLoading,
  },
  data() {
    return {
      detailsapptop: {
        id: "",
        img: "",
        name: "",
        author: "",
        label: "",
        // 收藏
        collect: "",
        // 人气
        click: "",
        // 推荐
        month_ticket_num: "",
        //每月推荐
        per_month_ticket_num: "",
        //字数
        total_words: "",
        //简介
        intro: "",
        like: "",
        is_vip: "",
        is_like: "",
        is_read: "",
      },
      serial: {
        name: "",
        new_section: "",
        id: "",
        serial_status: "",
      },
      recommendbox: [],
      serialrows: [],
      Myshow: localStorage.getItem("api_token"),
      achieve: false,
    };
  },
  beforeRouteUpdate(to, from, next) {
    next();
    if (to.fullPath !== from.fullPath) {
      this.getdetail(this.$route.params.id);
      this.gosec(this.$route.params.id);
      this.getcomments(this.$route.params.id);
    }
  },
  methods: {
    getdetail(id = this.id) {
      this.axios
        .get(`https://jyapi.jyacg.com/web/books/detail?id=${id}`, {
          headers: {
            authorization: localStorage.getItem("api_token"),
          },
        })
        .then((res) => {
          this.detailsapptop = {
            id: res.data.data.id,
            img: res.data.data.cover_image,
            name: res.data.data.name,
            author: res.data.data.author,
            label: res.data.data.label,
            // 收藏
            collect: res.data.data.collect,
            // 人气
            click: res.data.data.click,
            // 推荐
            month_ticket_num: res.data.data.month_ticket_num,
            //每月推荐
            per_month_ticket_num: res.data.data.per_month_ticket_num,
            //字数
            total_words: res.data.data.total_words,
            //简介
            intro: res.data.data.intro,
            like: res.data.data.like,
            is_vip: res.data.data.is_vip,
            is_like: res.data.data.is_like,
            is_read: res.data.data.is_read,
          };
          this.serial = {
            name: res.data.data.name,
            new_section: res.data.data.new_section,
            id: res.data.data.id,
            serial_status: res.data.data.serial_status,
          };
          this.$bus.$emit("MyLoading", false);
        });
    },
    gosec(id = this.id) {
      this.axios
        .get(`https://jyapi.jyacg.com/web/books/detail_sec?id=${id}`, {
          headers: {
            authorization: localStorage.getItem("api_token"),
          },
        })
        .then((res) => {
          if (res.status) {
            this.recommendbox = res.data.data.recommend.splice(0, 3);
          }
        });
    },
    getcomments(id = this.id) {
      this.axios
        .get(`https://jyapi.jyacg.com/web/books/review?id=${id}&page=1`)
        .then((res) => {
          if (res.status == 200) {
            this.serialrows = res.data.data.review_content;
          }
        });
    },
    add_review(content, reply_id) {
      if (this.achieve) {
        return
      }
      this.achieve = true
      this.axios
        .get(
          `https://jyapi.jyacg.com/web/books/add_review?books_id=${this.id}${reply_id}&content=${content}`,
          {
            headers: {
              authorization: localStorage.getItem("api_token"),
            },
          }
        )
        .then((res) => {
          if (res.status == 200) {
            this.achieve = false
            this.getcomments();
          }
        });
    },
  },
  mounted() {
    this.$bus.$emit("MyLoading", true);
    this.getdetail(this.id);
    this.gosec(this.id);
    this.getcomments(this.id);
    this.$bus.$on("add_review", this.add_review);
  },
};
</script>

<style lang="scss" scoped>
.title {
  padding-top: 0px;
  padding-bottom: 0px;
}
.recommend_title {
  margin: 12px 12.6px;
  font-weight: bold;
  font-size: 16px;
  color: #333;
}
</style>