<template>
  <div class="detailsapptop">
    <div class="detailstop">
      <div class="right">
        <img :src="detailsapptop.img" v-show="detailsapptop.label" /><img
          src="@/assets/img/zw.png"
          alt=""
          v-show="detailsapptop.label[2] ? detailsapptop.label[2].id == 102 : 0"
        />
      </div>
      <div class="letf">
        <div>
          <h4>{{ detailsapptop.name }}</h4>
          <img
            src="@/assets/img/vip.png"
            v-if="detailsapptop.is_vip"
            class="vip"
          /><img
            src="@/assets/img/dz.png"
            v-show="!detailsapptop.is_like"
            @click="Like"
          />
          <img
            src="@/assets/img/ydz.png"
            v-show="detailsapptop.is_like"
            @click="Like"
          />
        </div>
        <p>{{ detailsapptop.author }}&nbsp;&nbsp;&nbsp;著</p>
        <p>
          分类&nbsp;&nbsp;|&nbsp;&nbsp;<span @click="goclassdetails">{{
            detailsapptop.label ? detailsapptop.label[0].name : ""
          }}</span>
        </p>
        <p>
          {{ detailsapptop.collect }}&nbsp;收藏&nbsp;&nbsp;|&nbsp;&nbsp;{{
            moods
          }}&nbsp;人气&nbsp;&nbsp;|&nbsp;&nbsp;{{
            detailsapptop.month_ticket_num
          }}&nbsp;推荐
        </p>
        <p>
          {{ detailsapptop.total_words }}w&nbsp;字&nbsp;&nbsp;|&nbsp;&nbsp;{{
            detailsapptop.like
          }}&nbsp;赞&nbsp;&nbsp;|&nbsp;&nbsp;{{
            detailsapptop.per_month_ticket_num
          }}&nbsp;本月推荐
        </p>
      </div>
    </div>
    <div class="reward_box">
      <div>
        <img src="@/assets/img/xnb.png" /><span @click="Iflogin">打赏道具</span>
      </div>
      <div>
        <img src="@/assets/img/piao.png" /><span @click="Iflogin"
          >打赏月票</span
        >
      </div>
    </div>
    <div class="introduction">
      <h4>简介</h4>
      <div>
        <p :style="{ height: deploy ? 'auto' : '35px' }">
          {{ detailsapptop.intro }}
        </p>
        <span @click="deploy = !deploy">{{
          deploy ? "收起简介" : "展开简介"
        }}</span>
      </div>
    </div>
    <div class="label_box">
      <div v-for="(item, index) in detailsapptop.label" :key="index">
        <div v-if="item.style != 4">{{ item.name }}</div>
      </div>
    </div>
    <div class="footerdata">
      <van-button type="default" @click="getfavorites"
        >{{ detailsapptop.is_read ? "已" : "" }}加入书架</van-button
      >
      <router-link
        :to="{
          name: 'read',
          query: {
            id: detailsapptop.id,
            page: 1,
          },
        }"
      >
        <van-button
          type="info"
          :style="{
            backgroundColor: `${detailsapptop.is_vip ? '#fe2d46' : '#439fff'}`,
          }"
          >立刻阅读
        </van-button>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "Detailsapptop",
  props: ["detailsapptop", "getdetail"],
  data() {
    return {
      deploy: false,
    };
  },
  computed: {
    moods() {
      if (!this.detailsapptop) {
        return;
      }
      return (this.detailsapptop.click / 10000).toFixed(2) + "w";
    },
  },

  methods: {
    Iflogin() {
      if (!localStorage.getItem("api_token")) {
        this.$bus.$emit("Iflogin");
        return;
      }
    },
    getfavorites() {
      if (!localStorage.getItem("api_token")) {
        this.$bus.$emit("Iflogin");
        return;
      }
      if(this.detailsapptop.is_read){
        this.$toast('本书已收藏')
        return
      }
     
      this.axios.post(
        "https://jyapi.jyacg.com/web/books/favorites",
        { books_id: this.detailsapptop.id },
        {
          headers: {
            authorization: localStorage.getItem("api_token"),
          },
        }
      ).then(res=>{
        if (res.status == 200) {
          this.$toast('成功加入书架')
          console.log(res.data);
            this.getdetail();
          }
      });
    },
    Like() {
      if (!localStorage.getItem("api_token")) {
        this.$bus.$emit("Iflogin");
        return;
      }
      
      this.axios
        .post(
          "https://jyapi.jyacg.com/web/books/like",
          {
            books_id: this.detailsapptop.id,
          },
          {
            headers: {
              authorization: localStorage.getItem("api_token"),
            },
          }
        )
        .then((res) => {
          if (res.status == 200) {
         
            this.$toast(res.data.data?res.data.data:'点赞成功')
            this.getdetail();
          }
        });
    },
    goclassdetails() {
      localStorage.setItem("title", this.detailsapptop.label[0].name);
      localStorage.setItem(
        "param",
        JSON.stringify({
          channel: this.detailsapptop.label[0].id,
          serial_status: "",
          charge_type: "",
          page: 1,
          limit: 200,
        })
      );
      this.$router.push({ name: "classdetails" });
    },
  },
};
</script>

<style lang="scss" scoped>
.detailsapptop {
  width: 350px;
  min-height: 440px;
  margin: 16px 12.5px;
  background-color: #fff;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  .detailstop {
    height: 160px;
    display: flex;
    align-items: flex-end;
    .right {
      position: relative;
      margin: 0px 15px;
      height: 140px;
      width: 86px;
      img:nth-child(1) {
        height: 127px;
        width: 86px;
      }
      img:nth-child(2) {
        position: absolute;
        width: 20px;
        height: 30px;
        top: -15px;
        left: -10px;
      }
    }
    .letf {
      height: 140px;
      div {
        height: 17.5px;
        display: flex;
        h4 {
          width: 125px;
          font-weight: bold;
          font-size: 15px;
          color: #333;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        margin-bottom: 12px;
        .vip {
          width: 30px;
          height: 12px;
          margin: 0px;
        }
        img {
          height: 18px;
          margin-left: 15px;
        }
      }
      p {
        margin-bottom: 12px;
        color: #666;
        font-size: 13px;
        line-height: 1.1;

        span {
          display: inline-block;
          color: #fea238;
          background-color: #ffeed9;
          font-size: 12px;
          padding: 5px;
          border-radius: 5px;
        }
      }
    }
  }
  .reward_box {
    width: 100%;
    height: 27px;
    margin-top: 12px;
    padding-bottom: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-bottom: 1px solid #eee;
    div {
      align-items: center;
      display: flex;
      img {
        width: 27px;
        height: 27px;
      }
      span {
        display: inline-block;
        color: #333;
        font-size: 12px;
        margin-left: 5px;
      }
    }
  }
  .introduction {
    margin: 15px 12px 0px;
    h4 {
      margin-bottom: 15px;
      width: 125px;
      font-weight: bold;
      font-size: 15px;
      color: #333;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    div {
      font-size: 13px;
      color: #999;
      height: auto;
      border-bottom: 1px solid #e4e4e4;
      padding-bottom: 7.5px;
      p {
        overflow: hidden;
        line-height: 20px;
        margin-bottom: 5px;
      }
      span {
        font-size: 12px;
      }
    }
  }
  .label_box {
    width: 326px;
    min-height: 25px;
    margin: 15px 12px 0px;
    flex-wrap: wrap;
    display: flex;
    div {
      div {
        width: auto;
        background: #f7f7f7;
        height: 25px;
        padding: 0px 6px;
        margin-right: 7px;
        font-size: 12px;
        color: #666;
        border-radius: 5px;
        background-color: #f7f7f7;
        line-height: 25px;
      }
    }
  }
  .footerdata {
    display: flex;
    height: 46px;
    margin-top: 15px;
    padding-bottom: 13px;
    justify-content: space-around;
    .van-button {
      color: #fff;
      width: 150px;
      border-radius: 7px;
      border: none;
    }
    .van-button:nth-child(1) {
      background-color: #abc3d1;
    }
    .van-button:nth-child(2) {
      background-color: #439fff;
    }
  }
}
</style>